import _ from 'lodash';
import classNames from 'class-names';
import React, { Component } from 'react';
import './App.css';
import ReactHighcharts from 'react-highcharts';
import Slider from 'rc-slider';
import Icon from 'react-oui-icons';

import constants from './constants';
import fns from './fns';

import 'rc-slider/assets/index.css';

const colors = [
  '#ffda7c',
  '#ff6090',
  '#9a67ea',
  '#6ec6ff',
]

class DistributionChart extends Component {
  getDistributionData = () => {
    const {
      counterfactualEnabled,
      counts,
      distributions,
      position,
      sampleRate,
      showTotals,
      variations,
    } = this.props;

    const series = [];
    
    Array(variations).fill(1).forEach((value, index) => {
      series.push({
        color: colors[index],
        name: !!index ? `Variation ${index}` : 'Original',
        data: distributions[index].slice(0, Math.ceil(position / sampleRate) + 1),
        animation: {
          duration: 0,
        },
      })
    });
    
    if (counterfactualEnabled) {
      const conversionRateSeries = [];
      const counterfactualRateSeries = [];
      const totalConversions = [];
      const counterfactualConversions = [];
      let index = 0;
      while (index <= position) {
        conversionRateSeries.push(counts[index].totalConversionRate);
        counterfactualRateSeries.push(counts[index].counterfactualRate);
        totalConversions.push(counts[index].totalConversions);
        counterfactualConversions.push(counts[index].counterfactualConversions);
        index += sampleRate;
      }

      series.push({
        animation: {
          duration: 0,
        },
        color: 'black',
        dashStyle: 'solid',
        data: conversionRateSeries,
        lineWidth: 2,
        marker: {
          enabled: false
        },
        name: 'Total Conversion Rate',
        tooltip: {
          split: true,
          valueDecimals: 2,
          valueSuffix: '%',
        },
        type: 'spline',
        yAxis: 1,
      });

      series.push({
        animation: {
          duration: 0,
        },
        color: 'black',
        dashStyle: 'shortdot',
        data: counterfactualRateSeries,
        lineWidth: 2,
        marker: {
          enabled: false
        },
        name: 'Counterfactual Conversion Rate',
        tooltip: {
          split: true,
          valueDecimals: 2,
          valueSuffix: '%',
        },
        type: 'spline',
        yAxis: 1,
      });

      if (showTotals) {
        series.push({
          animation: {
            duration: 0,
          },
          color: 'white',
          data: totalConversions,
          lineWidth: 2,
          marker: {
            enabled: false
          },
          name: 'Total Conversions',
          tooltip: {
            split: true,
          },
          type: 'spline',
          yAxis: 2,
        });

        series.push({
          animation: {
            duration: 0,
          },
          color: 'white',
          dashStyle: 'shortdot',
          data: counterfactualConversions,
          lineWidth: 2,
          marker: {
            enabled: false
          },
          name: 'Counterfactual Conversions',
          tooltip: {
            split: true,
          },
          type: 'spline',
          yAxis: 2,
        });
      }
    }

    return _.cloneDeep(series);
  }

  render() {
    const {
      chartRef,
      counterfactualEnabled,
      distributions,
      policy,
      sampleRate,
      showTotals,
    } = this.props;

    let config = {
      chart: {
        // backgroundColor: 'lightgray',
        type: counterfactualEnabled ? 'area' : 'line',
      },
      credits: {
        enabled: false,
      },
      plotOptions: {
        line: {
          marker: {
            enabled: false,
          },
        },
      },
      tooltip: {formatter: function () {
        // The first returned item is the header, subsequent items are the
        // points
        return ['<b>' + this.x * sampleRate + ' visitors</b>'].concat(
            this.points.map(function (point) {
              let pointValue = point.y;
              let suffix = '% of Traffic';
              if (point.series.name.includes('Rate')) {
                suffix = '%';
              }
              if (!point.series.name.includes('Conversions')) {
                pointValue = Number(point.y).toFixed(2) + suffix;
              }
              return point.series.name + ': ' + pointValue;
            })
          );
        },
        split: true,
        valueSuffix: '% of Traffic',
      },
      series: this.getDistributionData(),
      title: {
        text: constants.CHART_LABELS[policy],
      },
      xAxis: {
        labels: {
          formatter: function() {
            return this.value * sampleRate;
          }
        },
        min: 0,
        max: parseInt(distributions[0].length),
      },
      yAxis: [
        {
          labels: {
            formatter: function() {
              return this.value + '%';
            },
          },
          title: {
            text: 'Distribution',
          },
        },
      ],
    }

    if (counterfactualEnabled) {
      config.plotOptions = {
        area: {
          lineWidth: 0,
          fillOpacity: 1,
          marker: {
            enabled: false
          },
          stacking: 'percent',
        },
      }

      config.yAxis.push({
        opposite: true,
        labels: {
          formatter: function() {
            return this.value + '%';
          },
        },
        // max: 60,
        // min: 40,
        title: {
          style: {
            color: 'black',
          },
          text: 'Conversion Rate',
        },
      })
    }

    if (showTotals) {
      config.yAxis.push({
        opposite: true,
        // max: 12000,
        title: {
          style: {
            color: 'white',
          },
          text: 'Conversions',
        },
      });
    }

    return (
      <div className="flex--1">
        <ReactHighcharts
          config={ config }
          ref={ chartRef }
        />
      </div>
    );
  }
}

class VariationCard extends Component {
  render() {
    const {
      color,
      conversionCount,
      title,
      variationColor,
      visitorCount,
    } = this.props;

    return (
      <div
        className="flex flex--column flex-align--center soft--sides">
        <h5 style={ { 'color': variationColor } }>
          { title }
        </h5>
        <p className="micro muted">
          Conversions
        </p>
        <h3 className={ `${color}` }>
          { conversionCount }
        </h3>
        <p className="micro muted">
          Visitors
        </p>
        <h3 className={ `${color}` }>
          { visitorCount }
        </h3>
        <p className="micro muted">
          Conversion Rate
        </p>
        <h3 className={ `${color}` }>
          { `${visitorCount ? ((conversionCount / visitorCount) * 100).toFixed(2) : '0.00'}%` }
        </h3>
      </div>
    )
  }
}

class StatSigCard extends Component {
  formatPValue = () => {
    const { pValue } = this.props;

    return `${ ((1 - pValue) * 100).toFixed(2) }%`;
  }

  render() {
    const {
      color,
      pValue,
      title,
    } = this.props;

    return (
      <div className="flex flex--column flex-align--center soft--sides width--50">
        <h5>
          { title }
        </h5>
        <h3 className={ classNames({
          [color]: pValue <= 0.10
        }) }>
          { this.formatPValue() }
        </h3>
      </div>
    );
  }
}

class SummaryCard extends Component {
  getVisitorCount = (variation) => {
    const {
      counts,
      position,
    } = this.props;

    return counts[position].visitors[variation];
  }

  getConversionCount = (variation) => {
    const {
      counts,
      position,
    } = this.props;

    return counts[position].conversions[variation];
  }

  getPValue = (variation) => {
    const {
      confidences,
      position,
    } = this.props;

    return confidences[position].pValues[variation];
  }

  isBetterThanBaseline = (variation) => {
    const baselineConversionRate = this.getConversionCount(0) / this.getVisitorCount(0);
    const variationConversionRate = this.getConversionCount(variation) / this.getVisitorCount(variation);
    return Number.isNaN(baselineConversionRate) || Number.isNaN(variationConversionRate) ? false : variationConversionRate > baselineConversionRate;
  };

  renderVariationCards = () => {
    const { variations } = this.props;

    let conversionTotal = 0;
    let visitorTotal = 0;

    return Array(variations + 1).fill(1).map((value, index) => {
      if (index < variations) {
        conversionTotal += this.getConversionCount(index);
        visitorTotal += this.getVisitorCount(index);
      }
      if (!index) {
        return (
          <VariationCard
            color="black"
            conversionCount={ this.getConversionCount(0) }
            key={ index }
            title="Original"
            variationColor={ colors[index] }
            visitorCount={ this.getVisitorCount(0) }
          />
        );
      } else if (index === variations) {
        return (
          <VariationCard
            color="black"
            conversionCount={ conversionTotal }
            key={ index }
            title="Total"
            variationColor='black'
            visitorCount={ visitorTotal }
          />
        );
      } else {
        return (
          <VariationCard 
            color={ this.getPValue(index - 1) <= 0.10 ? this.isBetterThanBaseline(index) ? 'green' : 'red' : 'black' }
            conversionCount={ this.getConversionCount(index) }
            key={ index }
            title={ `Variation ${index}` }
            variationColor={ colors[index] }
            visitorCount={ this.getVisitorCount(index) }
          />
        );
      }
    });
  };

  renderStatSigCards = () => {
    const { variations } = this.props;

    return Array(variations - 1).fill(1).map((value, index) => {
      return (
        <StatSigCard
          color={ this.isBetterThanBaseline(index + 1) ? 'green' : 'red' }
          key={ index }
          pValue={ this.getPValue(index) }
          title={ `Variation ${index + 1}` }
        />
      )
    });
  };

  render() {
    const { hideGraphs, policy, variations } = this.props;
    return (
      <div
        className={ classNames({
          "border--left": !hideGraphs,
          "flex": true,
          "flex-align--center": true,
          "flex--column": true,
          "soft": hideGraphs,
        }) }
        style={ {
          minWidth: `${125 * variations}px`,
        } }
      >
        { hideGraphs && (
          <h1>{ constants.CHART_LABELS[policy] }</h1>
        ) }
        <div className="flex flex--row flex--space width--100">
          { this.renderVariationCards() }
        </div>
        <h4 className="flex">
          Statistical Significance:
        </h4>
        <div className="flex flex--row flex--space width--100">
          { this.renderStatSigCards() }
        </div>
      </div>
    )
  }
}

class App extends Component {
  constructor(props) {
    super(props);

    this.sampleRate = window.uniform.length / 500;
    this.minRegretDistributions = fns.getDistributions(window.minRegret, this.sampleRate);
    this.minTimeDistributions = fns.getDistributions(window.minTime, this.sampleRate);
    this.uniformDistributions = fns.getDistributions(window.uniform, this.sampleRate);
    this.minRegretCounts = fns.getCounts(window.minRegret);
    this.minTimeCounts = fns.getCounts(window.minTime);
    this.uniformCounts = fns.getCounts(window.uniform);
    this.minRegretConfidences = fns.getConfidenceNumbers(window.minRegret);
    this.minTimeConfidences = fns.getConfidenceNumbers(window.minTime);
    this.uniformConfidences = fns.getConfidenceNumbers(window.uniform);
    this.maxVisitors = Math.max(window.minRegret.length, window.minTime.length, window.uniform.length);
    this.minRegretVariations = window.minRegret[0].trafficAllocation.length;
    this.minTimeVariations = window.minTime[0].trafficAllocation.length;
    this.uniformVariations = window.uniform[0].trafficAllocation.length;
    this.chartRefs = {
      minRegretRef: React.createRef(),
      minTimeRef: React.createRef(),
      uniformRef: React.createRef(),
    }
    this.marks = {
      0: {
        style: {
          color: 'grey',
        },
        label: 'Start',
        category: 'ALL',
      },
      [this.maxVisitors]: {
        style: {
          color: 'grey',
        },
        label: 'End',
        category: 'ALL',
      },
    };
    const significance = {
      'minRegret': new Array(this.minRegretVariations - 1).fill(false),
      'minTime': new Array(this.minTimeVariations - 1).fill(false),
      'uniform': new Array(this.uniformVariations - 1).fill(false),
    }

    const distributions = new Array(this.minTimeVariations).fill(true);

    for (let index = 0; index < this.maxVisitors; index++) {
      const nextTick =  index + (this.sampleRate - index % this.sampleRate) + this.sampleRate * 2;
      if (index) {
        for (let varIndex = 0; varIndex < this.minRegretVariations; varIndex++) {
          if (window.minTime[index].trafficAllocation[varIndex] / window.minTime[index - 1].trafficAllocation[varIndex] < .10) {
            this.marks[nextTick] = {
              style: {
                color: 'gray',
                top: '-35px',
              },
              label: 'Min Time Reallocation',
              category: constants.MIN_TIME,
              key: nextTick,
            }
          }
        }
      }
      for (let varIndex = 0; varIndex < this.uniformVariations - 1; varIndex++) {
        if (significance.minRegret[varIndex] !== window.minRegret[index].stats[(varIndex + 1).toString()].isSignificant) {
          if (window.minRegret[index].stats[(varIndex + 1).toString()].confInterval.lower > 0) {
            this.marks[nextTick] = {
              style: {
                color: 'green',
              },
              label: 'Min Regret Significant',
              category: constants.MIN_REGRET,
              key: nextTick,
            }
          } else {
            this.marks[nextTick] = {
              style: {
                color: 'red',
              },
              label: 'Min Regret Significant',
              category: constants.MIN_REGRET,
              key: nextTick,
            }
          }
          significance.minRegret[varIndex] = !significance.minRegret[varIndex];
        }

        if (significance.minTime[varIndex] !== window.minTime[index].stats[(varIndex + 1).toString()].isSignificant) {
          if (window.minTime[index].stats[(varIndex + 1).toString()].confInterval.lower > 0) {
            this.marks[nextTick] = {
              style: {
                color: 'green',
              },
              label: 'Min Time Significant',
              category: constants.MIN_TIME,
              key: nextTick,
            }
          } else {
            this.marks[nextTick] = {
              style: {
                color: 'red',
              },
              label: 'Min Time Significant',
              category: constants.MIN_TIME,
              key: nextTick,
            }
          }
          significance.minTime[varIndex] = !significance.minTime[varIndex];
        }

        if (significance.uniform[varIndex] !== window.uniform[index].stats[(varIndex + 1).toString()].isSignificant) {
          if (window.uniform[index].stats[(varIndex + 1).toString()].confInterval.lower > 0) {
            this.marks[nextTick] = {
              style: {
                color: 'green',
              },
              label: 'A/B Test Significant',
              category: constants.MANUAL,
              key: nextTick,
            }
          } else {
            this.marks[nextTick] = {
              style: {
                color: 'red',
              },
              label: 'A/B Test Significant',
              category: constants.MANUAL,
              key: nextTick,
            }
          }
          significance.uniform[varIndex] = !significance.uniform[varIndex];
        }
      }
    }
  }

  state = {
    counterfactualEnabled: false,
    currentPosition: 0,
    enabledCharts: [constants.MANUAL],
    hideGraphs: false,
    interval: null,
    isRunning: false,
    marks: {},
    multiplier: 100,
    showTotals: true,
  };

  componentDidMount() {
    document.addEventListener('keydown', this.handleKeyDown);
    this.setTimelineMarks();
  }

  getNextSignificantPoint = () => {
    const {
      currentPosition,
      marks,
    } = this.state;
    const significantPoints = Object.keys(marks).filter(mark => mark > currentPosition);
    return significantPoints.length ? Number(significantPoints[0]) : currentPosition;
  };

  getPreviousSignificantPoint = () => {
    const {
      currentPosition,
      marks,
    } = this.state;
    const significantPoints = Object.keys(marks).filter(mark => mark < currentPosition);
    return significantPoints.length ? Number(significantPoints[significantPoints.length - 1]) : currentPosition;
  };

  toggleCounterfactual = () => {
    this.setState((previousState) => {
      return {
        counterfactualEnabled: !previousState.counterfactualEnabled,
      };
    });
  };

  toggleSeries = (index) => {
    Object.values(this.chartRefs).forEach((ref) => {
      if (ref.current && ref.current.chart.series[index].visible) {
        ref.current.chart.series[index].hide();
      } else if (ref.current && !ref.current.chart.series[index].visible) {
        ref.current.chart.series[index].show();
      }
    });
  };

  toggleTotals = () => {
    this.setState((previousState) => {
      return {
        showTotals: !previousState.showTotals,
      };
    });
  };

  handleKeyDown = (event) => {
    switch( event.keyCode ) {
      case 32:
        event.preventDefault();
        if (this.state.isRunning) {
          this.stopCounting();
        } else {
          this.startCounting();
        }
        break;
      case 48:
        this.toggleSeries(0);
        break;
      case 49:
        this.toggleSeries(1);
        break;
      case 50:
        this.toggleSeries(2);
        break;
      case 51:
        this.toggleSeries(3);
        break;
      case 65:
        this.toggleChart({ target: { value: constants.MANUAL } });
        break;
      case 67:
        this.toggleCounterfactual();
        break;
      case 69:
        this.stopCounting();
        this.setCurrentPosition(this.maxVisitors);
        break;
      case 71:
        this.toggleCharts();
        break;
      case 73:
        this.toggleChart({ target: { value: constants.MIN_REGRET } });
        break;
      case 76:
        this.toggleChart({ target: { value: constants.MIN_TIME } });
        break;
      case 78:
        this.stopCounting();
        this.setCurrentPosition(this.getNextSignificantPoint());
        break;
      case 80:
        this.stopCounting();
        this.setCurrentPosition(this.getPreviousSignificantPoint());
        break;
      case 82:
        this.reset();
        break;
      case 84:
        this.toggleTotals();
        break;
      default: 
        break;
    }
  };

  isChartEnabled = (chartType) => {
    return this.state.enabledCharts.includes(chartType);
  };

  reset = () => {
    this.stopCounting();
    this.setCurrentPosition(0);
  }

  setCurrentPosition = (position) => {
    this.setState(() => {
      return {
        currentPosition: position
      }
    });
  };

  setTimelineMarks = () => {
    let marks = _.cloneDeep(this.marks)
    const marksToDelete = [];

    Object.values(constants.ALLOCATION_TYPES).forEach((type) => {
      if (!this.isChartEnabled(type)) {
        _.map(marks, (entry) => {
          if (entry.category === type) {
            marksToDelete.push(entry.key);
          }
        });
      }
    });

    marksToDelete.forEach((key) => {
      delete marks[key];
    });

    this.setState(() => {
      return {
        marks,
      };
    });
  };

  startCounting = () => {
    if (this.state.currentPosition >= this.maxVisitors) {
      this.setState(() => {
        return {
          currentPosition: 0,
        };
      }, this.startInterval);
    } else {
      this.startInterval();
    }
    this.setState(() => {
      return {
        isRunning: true,
      };
    });
  };

  startInterval = () => {
    this.setState(() => {
      return {
        interval: setInterval(() => {
          if (this.state.currentPosition >= this.maxVisitors) {
            this.stopCounting();
            return;
          }
          this.setState((_previousState) => {
            return {
              currentPosition: _previousState.currentPosition + _previousState.multiplier >= this.maxVisitors ? this.maxVisitors : _previousState.currentPosition + _previousState.multiplier,
            };
          });
        }, 100),
      }
    });
  };

  stopCounting = () => {
    clearInterval(this.state.interval);
    this.setState(() => {
      return {
        interval: null,
        isRunning: false,
      };
    });
  };

  toggleChart = (event) => {
    const chartType = event.target.value;

    if (this.state.enabledCharts.includes(chartType)) {
      const charts = _.cloneDeep(this.state.enabledCharts);
      this.setState(() => {
        return {
          enabledCharts: charts.filter(type => type !== chartType),
        };
      }, this.setTimelineMarks);
    } else {
      this.setState((previousState) => {
        const charts = _.cloneDeep(previousState.enabledCharts)
        charts.push(chartType);
        return {
          enabledCharts: charts,
        };
      }, this.setTimelineMarks);
    }
  };

  toggleCharts = (event) => {
    this.setState((previousState) => {
      return {
        hideGraphs: !previousState.hideGraphs,
      }
    })
  };

  render() {
    return (
      <div className="soft--sides">
        <div className="push--ends flex flex--row flex-align--center width--100">
          <div className={ classNames({
            'push-double--right': true,
            'flex': true,
            'flex--column': true
          }) }>
            <div className="flex--row micro">
              <input
                checked={ this.isChartEnabled(constants.MANUAL) }
                id={ constants.MANUAL }
                onChange={ this.toggleChart }
                type="checkbox"
                value={ constants.MANUAL }
              />
              <label htmlFor={ constants.MANUAL }>
                A/B
              </label>
            </div>
            <div className="flex--row micro">
              <input
                checked={ this.isChartEnabled(constants.MIN_REGRET) }
                id={ constants.MIN_REGRET }
                onChange={ this.toggleChart }
                type="checkbox"
                value={ constants.MIN_REGRET }
              />
              <label htmlFor={ constants.MIN_REGRET }>
                Min Regret
              </label>
            </div>
            <div className="flex--row micro">
              <input
                checked={ this.isChartEnabled(constants.MIN_TIME) }
                id={ constants.MIN_TIME }
                onChange={ this.toggleChart }
                type="checkbox"
                value={ constants.MIN_TIME }
              />
              <label htmlFor={ constants.MIN_TIME }>
                Min Time
              </label>
            </div>
            <div className="flex--row micro">
              <input
                checked={ this.state.hideGraphs }
                id="charts"
                onChange={ this.toggleCharts }
                type="checkbox"
              />
              <label htmlFor="charts">
                Hide Graphs
              </label>
            </div>
          </div>
          <div className="push-double--right">
            { !this.state.interval && (
              <button
                className="no-padding"
                onClick={ this.startCounting }>
                <div className="flex square--50 flex-align--center flex-justify--center">
                  { this.state.currentPosition !== this.maxVisitors && (
                    <Icon
                      name="play"
                      size="large"
                    />
                  ) }
                  { this.state.currentPosition === this.maxVisitors && (
                    <Icon
                      name="refresh"
                      size="large"
                    />
                  ) }
                </div>
              </button>
            ) }
            { this.state.interval && (
              <button
                className="no-padding"
                onClick={ this.stopCounting }>
                <div className="flex square--50 flex-align--center flex-justify--center">
                  <Icon
                    name="pause"
                    size="large"
                  />
                </div>
              </button>
            ) }
          </div>
          <div className="flex flex--1 flex-align--center push-double--right">
            <Slider
              dots={ false }
              min={ 0 }
              marks={ this.state.marks }
              max={ this.maxVisitors }
              onChange={ this.setCurrentPosition }
              value={ this.state.currentPosition }
            />
          </div>
        </div>
        <div className={ classNames({
          'flex': this.state.hideGraphs,
          'flex--row': this.state.hideGraphs,
          'flex-justify--center': this.state.hideGraphs,
          'flex--space': this.state.hideGraphs,
        }) }>
          { this.isChartEnabled(constants.MANUAL) && (
            <div className="flex flex--row border push--ends">
              { !this.state.hideGraphs && (
                <DistributionChart
                  chartRef={ this.chartRefs.uniformRef }
                  counterfactualEnabled={ this.state.counterfactualEnabled }
                  counts={ this.uniformCounts }
                  distributions={ this.uniformDistributions }
                  policy={ constants.MANUAL }
                  position={ this.state.currentPosition }
                  sampleRate={ this.sampleRate }
                  showTotals={ this.state.showTotals }
                  variations={ this.uniformVariations }
                />
              ) }
              <SummaryCard
                confidences={ this.uniformConfidences }
                counts={ this.uniformCounts }
                hideGraphs={ this.state.hideGraphs }
                policy={ constants.MANUAL }
                position={ this.state.currentPosition }
                variations={ this.uniformVariations }
              />
            </div>
          ) }
          { this.isChartEnabled(constants.MIN_REGRET) && (
            <div className="flex flex--row border push--ends">
              { !this.state.hideGraphs && (
                <DistributionChart
                  chartRef={ this.chartRefs.minRegretRef }
                  counterfactualEnabled={ this.state.counterfactualEnabled }
                  counts={ this.minRegretCounts }
                  distributions={ this.minRegretDistributions }
                  policy={ constants.MIN_REGRET }
                  position={ this.state.currentPosition }
                  sampleRate={ this.sampleRate }
                  showTotals={ this.state.showTotals }
                  variations={ this.minRegretVariations }
                />
              ) }
              <SummaryCard
                confidences={ this.minRegretConfidences }
                counts={ this.minRegretCounts }
                hideGraphs={ this.state.hideGraphs }
                policy={ constants.MIN_REGRET }
                position={ this.state.currentPosition }
                variations={ this.minRegretVariations }
              />
            </div>
          ) }
          { this.isChartEnabled(constants.MIN_TIME) && (
            <div className="flex flex--row border push--ends">
              { !this.state.hideGraphs && (
                <DistributionChart
                  chartRef={ this.chartRefs.minTimeRef }
                  counterfactualEnabled={ this.state.counterfactualEnabled }
                  counts={ this.minTimeCounts }
                  distributions={ this.minTimeDistributions }
                  policy={ constants.MIN_TIME }
                  position={ this.state.currentPosition }
                  sampleRate={ this.sampleRate }
                  showTotals={ this.state.showTotals }
                  variations={ this.minTimeVariations }
                />
              ) }
              <SummaryCard
                confidences={ this.minTimeConfidences }
                counts={ this.minTimeCounts }
                hideGraphs={ this.state.hideGraphs }
                policy={ constants.MIN_TIME }
                position={ this.state.currentPosition }
                variations={ this.minTimeVariations }
              />
            </div>
          ) }
        </div>
      </div>
    );
  }
}

export default App;
